type SolvedProblemProps = {
  problemName: string;
  problem: React.ReactElement;
  importance: React.ReactElement;
  solution: React.ReactElement;
  withImage?: boolean;
  telgorithmSolution?: boolean;
};

export const SolvedProblem: FC<SolvedProblemProps> = ({
  problemName,
  problem,
  importance,
  solution,
  withImage,
  telgorithmSolution,
}) => {
  return (
    <SolvedProblemContainer>
      <Text.Title.Bold.md>{problemName}</Text.Title.Bold.md>
      <div>
        <SolvedProblemBox>
          <SolvedProblemContentBlock>
            <CircleQuestionIcon />
            <Text.Regular.Bold.lg>The Problem</Text.Regular.Bold.lg>
            <div>{problem}</div>
          </SolvedProblemContentBlock>
          <SolvedProblemContentBlock>
            <ShieldWarningIcon />
            <Text.Regular.Bold.lg>Why it Matters</Text.Regular.Bold.lg>
            <div>{importance}</div>
          </SolvedProblemContentBlock>
        </SolvedProblemBox>
        <SolvedProblemBox withImage={withImage}>
          <SolvedProblemContentBlock>
            <BadgeSuccessIcon />
            <Text.Regular.Bold.lg>
              {telgorithmSolution ? 'Telgorithmâ€™s Solution' : 'The Solution'}
            </Text.Regular.Bold.lg>
            <div>{solution}</div>
          </SolvedProblemContentBlock>
          {withImage && (
            <SolvedProblemImage>
              <StaticImage
                src={'../../images/CustomerStories/Solution.webp'}
                alt="Boulevard"
                placeholder="none"
              ></StaticImage>
            </SolvedProblemImage>
          )}
        </SolvedProblemBox>
      </div>
    </SolvedProblemContainer>
  );
};

